* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.app {
    height: 100vh;
    width: 100vw;
    background: url("./resources/img/background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.navbar {
    display: inline-flex;
    align-items: center;
    min-width: 100%;
}

.nav-title {
    color: rgb(255, 244, 236);
    text-shadow: 2px 2px 2px rgb(158, 76, 21);
    padding: 20px 0;
    width: 100%;
    text-align: center;
    font-family: 'Dancing Script', cursive;
	font-size: 40px;
}

.nav-edit-user-bt, 
.nav-logout-bt {
    width: 100px;
    margin: 0 5px;
    text-decoration: none;
    list-style: none;
    align-items: right;
}