.card {
    background-image: url("../../resources/img/card.jpg");
    background-size:contain;
    display: flex;
    width: 200px;
    margin: 10px;
    border: 2px solid black;
    flex-direction: column;
    justify-content: space-between;
    align-content: center;
}

.img-container {
    justify-content: center;
    overflow: hidden;
    height: 200px;
}

.card-title {
    text-align: center;
}

.card-text-container {
    justify-content: center;
    display: inline-flex;
    flex-wrap: wrap;
}

.card-text {
    margin: 10px;
    text-align: center;
}

.img-container .img-to-fit {
    flex: 1;
    justify-content: center;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.buttons-recipe-card {
    display: flex;
    margin: 10px;
    justify-content: space-between;
}